/**
 * 
 * @param {Object} option enum
 * @param {Int} dbValue 
 * @returns 
 */
export const findOptionForDbValue = (option, dbValue) => {
  const item = option.find(element => {
    return element.dbValue === dbValue
  });
  return item;
}
/**
 * JavaenumEnum
 * @param {Integer} dbValue 
 * 
 * @returns 
 */
export const parsOrderStatusDbValue = (dbValue) => {
  switch(dbValue) {
    case -1:
      return `UNPROVED`;
      
    case 0:
      return `NEW`;

      case 1:
        return `PRINTED`;

      case 2:
        return `POSTED`;

      case 3:
        return `COLLECTED`;

      case 4:
        return `CONFIRMED`;

      case 5:
        return `FINISH`;

      case 6:
        return `REPORT_WAITING`;

      case 7:
        return `REPORT_WAITING`;

      case 8:
        return `REPORT_PROCESSING`;

      case 9:
        return `REPORT_PROCESSED`;

      case 10:
        return `REPORT_REJECTED`;

      default:
        return `NEW`;

  }
}

