export const parsePackagingDbValue = (dbValue) => {
  switch (dbValue) {
    case `default`:
      return `DEFAULT`;
    case `plastic`:
      return `PLASTIC`;
    case `suede`:
      return `suede`;

    default:
      return `DEFAULT`;
  }
}

