export const parsePhotoSizeDbValue = (dbValue) => {
  switch(dbValue) {
    case `default`:
      return `DEFAULT`;
    case `3inch`:
      return `THREE_INCH`;
    case `4inch`:
      return `FOUR_INCH`;

    case `5inch`:
      return `FIVE_INCH`;

    case `6inch`:
      return `SIX_INCH`;

    case `7inch`:
      return `SEVEN_INCH`;
      
    default:
      return `DEFAULT`;
      

  }
}

export const toRedableOptionText = (options, dbValue) => {
  const option = options.filter(item => {
    if(item.dbValue === dbValue) {
      return true;
    }
    return false;
  });

  if(option.length > 0) {
    return option[0].text;
  }
  return "";
}


