// ajax ,  axios
import axios from 'axios'
import { useNavigate } from 'react-router-dom';
import webConf from '../config/webConf'
import ToastHelper from './toastHelper';


const config = {
  baseURL: webConf.BASE_URL,
  timeout: 5 * 1000, //   
  crossDomain: true,
  withCredentials: true, // Check cross-site Access-Control
  // http
  // return truenullundefinedpromiseresolved,rejected
  validateStatus(status) {
    return status >= 200 && status < 510
  },
  handleError: (res) => {
    if(res.status === 401) {
      ToastHelper.showError("", () => {
        window.location = "/admin"
      });
      setTimeout(() => {
        window.location = "/admin"
      }, 500);
    }
    //  else {
    //   window.location = "/admin"
    // }
  }
}

// const retryTime = 2 // 
// const retryDelay = 1500 // 

// 
axios.defaults.withCredentials = true;
const _axios = axios.create(config)

_axios.interceptors.request.use(
  originConfig => {
    //  API 
    // Vue.prototype.$_lin_jump()
    const reqConfig = { ...originConfig }

    // step1: 
    if (!reqConfig.url) {
      /* eslint-disable-next-line */
      console.error('request need url')
      throw new Error({
        source: 'axiosInterceptors',
        message: 'request need url',
      })
    }

    if (!reqConfig.method) {
      //  get 
      reqConfig.method = 'get'
    }
    // 
    reqConfig.method = reqConfig.method.toLowerCase()

    // 
    if (reqConfig.method === 'get') {
      if (!reqConfig.params) {
        // 
        reqConfig.params = reqConfig.data || {}
      }
    } else if (reqConfig.method === 'post') {
      if (!reqConfig.data) {
        // 
        reqConfig.data = reqConfig.params || {}
      }

      // ,  formData 
      let hasFile = false
      Object.keys(reqConfig.data).forEach(key => {
        if (typeof reqConfig.data[key] === 'object') {
          const item = reqConfig.data[key]
          if (item instanceof FileList || item instanceof File || item instanceof Blob) {
            hasFile = true
          }
        }
      })

      //  FormData 
      if (hasFile) {
        const formData = new FormData()
        Object.keys(reqConfig.data).forEach(key => {
          formData.append(key, reqConfig.data[key])
        })
        reqConfig.data = formData
      }
    } else {
      // TODO: 
      /* eslint-disable-next-line */
      console.log("--- put")
      console.warn(`: ${reqConfig.method}, `)
    }
    return reqConfig
  },
  error => {
    console.log("error when request...")
    Promise.reject(error)
  },
)

// Add a response interceptor
_axios.interceptors.response.use(
  async res => {
    let { code, message } = res.data // eslint-disable-line
    if (res.status.toString().charAt(0) === '2') {
      return res.data
    }
    return new Promise(async (resolve, reject) => {
      // const { url } = res.config

      if (res.config.handleError) {
        return res.config.handleError(res)
      }
      reject()
    })
  },
  error => {
    // 
    if(error.code === 'ECONNABORTED' && error.message.indexOf(webConf.TIMEOUT_MSG) !== -1) {
      ToastHelper.showError(webConf.TIMEOUT_MSG);
    }
    console.log(": " + error)
    return Promise.reject(error)
  },
)

/**
 * @param {string} url
 * @param {object} data
 * @param {object} params
 */
export function post(url, data = {}, params = {}) {
  return _axios({
    method: 'post',
    url,
    data,
    params,
  })
}

/**
 * @param {string} url
 * @param {object} params
 */
export function get(url, params = {}) {
  return _axios({
    method: 'get',
    url,
    params,
    timeoutErrorMessage: webConf.TIMEOUT_MSG
  })
}

/**
 * @param {string} url
 * @param {object} data
 * @param {object} params
 */
export function put(url, params = {}, data = {},) {
  return _axios({
    method: 'put',
    url,
    params,
    data,
  })
}

/**
 * @param {string} url
 * @param {object} params
 */
export function _delete(url, headers = {}, data = {}) {
  return _axios.delete(url,{
    params: headers,
    data: data
  })
}

export default _axios
