import React, { Component } from 'react'
import ImageUploading from 'react-images-uploading'
import Config from '../../config/webConf'
import { uploadImage } from '../../utils/fileUploader'
import myLogger from '../../utils/logger'
import { StringUtils } from '../../utils/StringUtils'
import ToastHelper from '../../utils/toastHelper'

export default class UploadPhoto extends Component {

  constructor(props) {
      super(props)
      this.state = {
          images: [],
          orderNumber: props.orderNumber
      }
      this.onImageUpload = this.onImageUpload.bind(this);
      this.onChange = this.onChange.bind(this);
      this.onImageRemoveAll = this.onImageRemoveAll.bind(this);
      this.onImageRemove = this.onImageRemove.bind(this);
      this.uploadAll = this.uploadAll.bind(this);
  }

  onImageUpload() {
    console.log("on image upload ")
  }

  onChange(imageList, addUpdateIndex) {
    this.setState({
        images: imageList
    });
  }

  onImageRemoveAll() {
    console.log("on image remove all")
  }

  onImageRemove() {
    console.log("on image remove")
  }

  async uploadAll() {
    console.log("upload all")
    let url = `/files/uploadMultiple` 

    let checkRepeat = {}
    let hasRepeat = false;
    let repeatName = "";

    let files = this.state.images.map( (item, index) => {
      const fileName = checkRepeat[item.file.name];
      if(!StringUtils.isEmpty(fileName)) {
        hasRepeat = true;
        repeatName = item.file.name; 
      }

      checkRepeat[item.file.name] = item.file.name;

      myLogger.debug("item: " + JSON.stringify(item.file));
      return item.file;
    });

    if(hasRepeat) {
      ToastHelper.showError("")
      return;
    }
    
    const response = await uploadImage(files, url, this.props.orderNumber);
    if(response.status.toLowerCase() !== 'success') {
      ToastHelper.showError(response.error || "");
      return;
    }
    console.log("response: " + JSON.stringify(response));
    ToastHelper.showDefault("");

    this.props.hideUpload();
    this.props.refreshPhotoList();
  }

  render() {
    const MAX_FILE_SIZE = 1024 * 1024 * 5
    const imageFiles = [];
    const maxNumber = 10;
    
    const getImageItem = () => {
      
    }
    return (
      <>
        <ImageUploading
          multiple
          value={this.state.images}
          onChange={this.onChange}
          maxNumber={maxNumber}
          dataURLKey="data_url"
        >
          {({
            imageList,
            onImageUpload,
            onImageRemoveAll,
            onImageUpdate,
            onImageRemove,
            isDragging,
            dragProps,
          }) => (
            // write your building UI
            <div className="card border-secondary mb-3" >
              <div className="card-header">.jpg, png</div>
              <div className="card-body">
                  <button
                  className='btn btn-secondary btn-sm'
                  style={isDragging ? { color: 'red' } : undefined}
                  onClick={onImageUpload}
                  {...dragProps}
                >
                  
                </button>
                &nbsp;
                {
                  this.state.images.length > 0 &&
                  <button className='btn btn-secondary btn-sm' onClick={onImageRemoveAll}></button>
                }
                &nbsp;
                <button className='btn btn-secondary btn-sm' onClick={this.props.hideUpload}></button>
                
                <div className='upload-container'>
                  {
                    imageList.map((image, index) => (
                      <div key={index} className="image-item" style={{height: `100px`}}>
                        <img src={image['data_url']} alt="" width="120" />
                        {
                            image.file.size > MAX_FILE_SIZE && 
                            <div className='img-item-warning-text'>
                              <span>5M</span>
                            </div>
                        }
                        <div className="image-item__btn-wrapper">
                          
                          <button className='btn btn-secondary btn-sm' onClick={() => onImageUpdate(index)}></button>
                          <button className='btn btn-secondary btn-sm' onClick={() => onImageRemove(index)}></button>
                        </div>
                      </div>
                    ))
                  }
                </div>
              </div>
            </div>
          )}
        </ImageUploading>

        {
          this.state.images.length > 0 &&
          <div>
            <button className='btn btn-primary btn-sm' onClick={this.uploadAll}>
              
            </button>
          </div>
        }
      </>
    )
  }
}
